}

.menu {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 8vh 0 2vh 0;
    z-index: 999999999;
    background: url(/my-page/static/media/bg.e92831d0.jpg) no-repeat center center fixed;
    background-size: cover;
}

.menu-item {
    display: inline-block;
    cursor: pointer;
    position: relative;
    margin: 0 2vw 0 0;
}


.menu-item:hover {
    text-shadow: 0 0 25px #ffffff;
}

.menu-no-underline {
    transition: all 0.3s ease-in-out 0s;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
}

.menu-item-active {
    width: 100%;
    height: 2px;
    background-color: #ffffff;
    position: absolute;
    bottom: 0;
    left: 0;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}

.menu-mobile {
    display: none;
}

.mobile-menu-item {
    display: block;
    position: relative;
    margin: 0 2vw 0 0;
    cursor: pointer;
    transition: all 0.5s ease-in-out 0s;
    -webkit-transform: translate(0,0);
            transform: translate(0,0);
}

.mobile-menu-item:hover {
    text-shadow: 0 0 25px #ffffff;
}


@media screen and (max-width: 600px) {
    .menu-wrapper {
        display: none;
    }

    .menu-mobile {
        display: block;
    }

    .menu {
        padding: 2vh 0 2vh 0;
    }
    
    .mobile-menu-item {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        margin: 1vh auto;
        font-size: 1.5rem;
    }

}
